'use strict';

var Curry = require("bs-platform/lib/js/curry.js");

function SemigroupoidExtensions(S) {
  var andThen = function (aToB, bToC) {
    return Curry._2(S.compose, bToC, aToB);
  };
  return {
          andThen: andThen
        };
}

function SemigroupoidInfix(S) {
  var andThen = function (aToB, bToC) {
    return Curry._2(S.compose, bToC, aToB);
  };
  var SE = {
    andThen: andThen
  };
  return {
          SE: SE,
          $less$less$less: S.compose,
          $great$great$great: andThen
        };
}

exports.SemigroupoidExtensions = SemigroupoidExtensions;
exports.SemigroupoidInfix = SemigroupoidInfix;
/* No side effect */
