'use strict';

var Curry = require("bs-platform/lib/js/curry.js");

function SemigroupExtensions(S) {
  var concatNamed = function (prefix, suffix) {
    return Curry._2(S.append, prefix, suffix);
  };
  return {
          concatNamed: concatNamed
        };
}

function SemigroupInfix(S) {
  return {
          $pipe$plus$pipe: S.append
        };
}

exports.SemigroupExtensions = SemigroupExtensions;
exports.SemigroupInfix = SemigroupInfix;
/* No side effect */
